@media (min-width: 992px) {
  .offers {
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
  }
  .offers-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .offers-left h1 {
    color: #171717;
    font-size: 80px;
    font-weight: 600;
  }
  .offers-left p {
    color: #171717;
    font-size: 22px;
    font-weight: 600;
  }
  .offers-left button {
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
  }
  .offers-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
  }
}

@media (max-width: 992px) {
  .offers {
    width: 100vw;
    height: 35vh;
    max-height: 35vh;
    padding: 15px 0px;
    display: flex;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
  }
  .offerImg {
    flex: 1;
    max-width: 240px;
  }
  .offers-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 16px;
  }
  .offers-left h1 {
    color: #171717;
    font-size: 1.5rem;
    font-weight: 600;
  }
  .offers-left p {
    color: #171717;
    font-size: 1rem;
    font-weight: 600;
    margin-top: 30px;
  }
  .offers-left button {
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
    padding: 8px;
  }
  .offers-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
