@media (min-width: 992px) {
  .loginsignup {
    width: 100%;
    height: 80vh;
    background-color: #fce3fe;
    padding-top: 100px;
  }
  .loginsignup-container {
    width: 580px;
    height: 600px;
    background: white;
    margin: auto;
    padding: 40px 60px;
  }
  .loginsignup-container h1 {
    margin: 20px 0px;
  }
  .loginsignup-fields {
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
  }
  .loginsignup-fields input {
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
  }
  .loginsignup-container button {
    width: 580px;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
  }
  .loginsignup-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
  }
  .loginsignup-login span {
    color: #ff4141;
    font-weight: 600;
  }
  .loginsignup-agree {
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
  }
}
@media (max-width: 992px) {
  .loginsignup {
    width: 100%;
    height: 80vh;
    background-color: #fce3fe;
    padding-top: 77px;
  }
  .loginsignup-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
  }
  .loginsignup-container h1 {
    padding-top: 25px;
    text-align: center;
    background-color: white;
  }
  .loginsignup-fields {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin-top: 30px;
    width: 90%;
  }
  .loginsignup-fields input {
    height: 72px;
    width: 100%;
    padding-left: 15px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
  }
  .loginsignup-container button {
    width: 95%;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    box-sizing: border-box;
  }
  .loginsignup-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 1rem;
    font-weight: 500;
  }
  .loginsignup-login span {
    color: #ff4141;
    font-weight: 600;
  }
  .loginsignup-agree {
    display: flex;
    align-items: center;
    max-width: 90vw;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 1rem;
    font-weight: 500;
  }
}
